$lineHeight: 24px;

.line {
  display: block;
  margin-bottom: 8px;
}

.line_wrapper {
  position: relative;
}

.title_wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 4px;
  white-space: nowrap;
}

.title {
  margin-right: 8px;
}

.element {
  color: white;
  background-color: white;
  height: $lineHeight;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
}

.line {
  width: 100%;
  height: auto;
}
.line_start,
.line_end {
  width: 20px;
  height: $lineHeight;
}
